:root {
  --theme-primary-color: #00884f;
  --theme-secondary-color: #006037;
  --theme-border-color: #cccccc;
}

:root .dark-theme {
  --theme-primary-color: #00884f;
  --theme-secondary-color: #014b2b;
  --theme-border-color: #ffffff;
}

a {
  text-decoration: none;
}

a,
a:hover {
  color: var(--theme-primary-color);
}

.palette {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 5px;
}

.palette p {
  height: 30px;
  width: 30px;
}

.palette .primary {
  background-color: var(--theme-primary-color);
}

.palette .secondary {
  background-color: var(--theme-secondary-color);
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.button {
  appearance: none;
  margin: 8px;
  font-size: 0.875rem;
  font-weight: 600;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 8px 16px;
  border-width: 1px;
  border-style: solid;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.3s;

  background-color: transparent;
  border-color: var(--theme-border-color);
  color: var(--theme-text-color);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08), 0 3px 6px rgba(0, 0, 0, 0.24);
}

.button :hover,
:focus,
:active {
  background-color: var(--theme-secondary-bg);
  color: var(--theme-primary-color);
}

.button.primary {
  background-color: var(--theme-primary-color);
  border-color: var(--theme-primary-color);
  color: #ffffff;
}

.button :hover,
.button :focus,
.button :active {
  background-color: var(--theme-primary-color);
  color: var(--theme-text-color);
}

table {
  color: var(--theme-text-color);
}

table tr,
table tr:nth-child(2n) {
  background-color: transparent;
}
